<template>
    <div>
      <div class="yo-scroll"
           :class="{'down':(state===0),'up':(state==1),refresh:(state===2),touch:touching}"
           @touchstart="touchStart($event)"
           @touchmove="touchMove($event)"
           @touchend="touchEnd($event)"
           @scroll="(onInfinite || infiniteLoading) ? onScroll($event) : undefined">
        <ul ref="chatBox" class="chat-box inner" :style="{ transform: 'translate3d(0, ' + top + 'px, 0)' }">
          <li class="pull-refresh">
            <slot name="pull-refresh">
              <span class="down-tip">下拉更新</span>
              <span class="up-tip">松开更新</span>
              <span class="refresh-tip">更新中</span>
            </slot>
          </li>

          <li><button @click="bottom">33</button></li>
          <li v-for="item in chatList" class="bubblebox clearfix">
            <img :class="item.position" :src="item.headPortraits" alt="">
            <p :class="item.position">
              <span :class="['nickname', 'nickname-' + item.position]">{{item.nickname}}</span>
              <span :class="['new-time', 'new-time-' + item.position]">{{item.newTime}}</span>
              <span :class="['bkbubble', item.position]">{{item.bkbubble}}</span>
            </p>
          </li>
        </ul>
      </div>
      <div class="chat-edit">
        <span></span>
        <input class="input-chat" type="text" v-model="chatmsg" placeholder="请输入您想说的话">
        <button @click="chatSend()" class="send-btn">发送</button>
      </div>
    </div>
</template>

<script>
export default {
  props: {
    offset: {
      type: Number,
      default: 40
    },
    enableInfinite: {
      type: Boolean,
      default: true
    },
    enableRefresh: {
      type: Boolean,
      default: true
    },
    onRefresh: {
      type: Function,
      default: undefined,
      required: false
    },
    onInfinite: {
      type: Function,
      default: undefined,
      require: false
    }
  },
  data() {
    return {
      chatmsg: '',
      chatList: [
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'left',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '88888888888咖啡泡进喧嚣的庭院333444444444444444444444444444444'
        },
        {
          position: 'right',
          headPortraits: '/src/assets/images/09.jpg',
          nickname: '某一位',
          newTime: '19:20:41',
          bkbubble: '咖啡泡进喧嚣的庭院'
        }
    ],
      top: 0,
      state: 0,
      startY: 0,
      touching: false,
      infiniteLoading: false
    }
  },
  methods: {
    chatSend() {
      let length = this.$refs.chatBox.children.length
      console.log(length)
      let newChat = {
        position: 'right',
        headPortraits: '/src/assets/images/08.jpg',
        nickname: '某一位',
        newTime: '19:20:41',
        bkbubble: this.chatmsg
      }
      this.chatList.push(newChat)
      this.chatmsg = ''
      this.$nextTick(function () {
        this.$refs.chatBox.scrollTop = this.$refs.chatBox.scrollHeight;
      })
//      let length = this.$refs.chatBox.children.length
//      this.$refs.chatBox.scrollTop = this.$refs.chatBox.scrollHeight;
    },
    bottom() {
      this.$refs.chatBox.scrollTop = this.$refs.chatBox.scrollHeight;
      let outerHeight = this.$el.clientHeight
      console.log('outerHeight',outerHeight)
      let scrollTop = this.$refs.chatBox.scrollTop
      console.log('scrollTop',scrollTop)
      let ptrHeight = this.onRefresh ? this.$refs.chatBox.querySelector('.pull-refresh').clientHeight : 0
      console.log('ptrHeight我也母鸡这是什么  ',ptrHeight)
      let bottom = outerHeight - scrollTop - ptrHeight
      console.log('bottom33',bottom)
    },
    touchStart(e) {
      this.startY = e.targetTouches[0].pageY
      this.startScroll = this.$el.scrollTop || 0
      this.touching = true
    },
    touchMove(e) {
      let outerHeight = this.$el.clientHeight
      console.log('outerHeight',outerHeight)
      let scrollTop = this.$refs.chatBox.scrollTop
      console.log('scrollTop',scrollTop)
      let ptrHeight = this.onRefresh ? this.$refs.chatBox.querySelector('.pull-refresh').clientHeight : 0
      console.log('ptrHeight我也母鸡这是什么  ',ptrHeight)
      let bottom = outerHeight - scrollTop - ptrHeight
      console.log('bottom33',bottom)
      if(this.$refs.chatBox.scrollTop <= 0) {
        if (!this.enableRefresh || this.$el.scrollTop > 0 || !this.touching) {
          return
        }
        let diff = e.targetTouches[0].pageY - this.startY - this.startScroll
        if (diff > 0) e.preventDefault()
        this.top = Math.pow(diff, 0.8) + (this.state === 2 ? this.offset : 0)

        if (this.state === 2) { // in refreshing
          return
        }
        if (this.top >= this.offset) {
          this.state = 1
        } else {
          this.state = 0
        }
      }
//      if (!this.enableRefresh || this.$el.scrollTop > 0 || !this.touching) {
//        return
//      }
//      let diff = e.targetTouches[0].pageY - this.startY - this.startScroll
//      if (diff > 0) e.preventDefault()
//      this.top = Math.pow(diff, 0.8) + (this.state === 2 ? this.offset : 0)
//
//      if (this.state === 2) { // in refreshing
//        return
//      }
//      if (this.top >= this.offset) {
//        this.state = 1
//      } else {
//        this.state = 0
//      }

    },
    touchEnd(e) {
      if (!this.enableRefresh) return
      this.touching = false
      if (this.state === 2) { // in refreshing
        this.state = 2
        this.top = this.offset
        return
      }
      if (this.top >= this.offset) { // do refresh
        this.refresh()
      } else { // cancel refresh
        this.state = 0
        this.top = 0
      }
    },
    refresh() {
      this.state = 2
      this.top = this.offset
      let _this = this
      this.state = 0
      this.top = 0
      console.log('333')
    },
    refreshDone() {
      this.state = 0
      this.top = 0
    },

    infinite() {
      this.infiniteLoading = true
      this.onInfinite(this.infiniteDone)
    },

    infiniteDone() {
      this.infiniteLoading = false
    },

    onScroll(e) {
      if (!this.enableInfinite || this.infiniteLoading) {
        return
      }
      let outerHeight = this.$el.clientHeight
      console.log('outerHeight',outerHeight)
      let innerHeight = this.$el.querySelector('.inner').clientHeight
      console.log('innerHeight',innerHeight)
      let scrollTop = this.$el.scrollTop
      console.log('scrollTop',scrollTop)
      let ptrHeight = this.onRefresh ? this.$el.querySelector('.pull-refresh').clientHeight : 0
      console.log('ptrHeight',ptrHeight)
      let infiniteHeight = this.$el.querySelector('.load-more').clientHeight
      console.log('infiniteHeight',infiniteHeight)
      let bottom = innerHeight - outerHeight - scrollTop - ptrHeight
      console.log('bottom',bottom)
      if (bottom < infiniteHeight) this.infinite()
    }
  }
}
</script>
<style  lang="scss" scoped>
  @import '../assets/scss/layout.scss';
  @import '../assets/scss/transition.scss';

  .chat-edit {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 150px;
    text-align: center;
    background-color: #242b33;
    input,
    button,
    span {
      position: absolute;
      display: inline-block;
      top: 25%;
    }
    input,
    button {
      height: 73px;
      border: 1px solid #85817e;
      border-radius: 50px;
      outline: none;
    }
    input {
      left: 13.5%;
      width: 60%;
      text-indent: 40px;
      color: #313131;
      background-color: #e6e6e6;
    }
    button {
      right: 3%;
      width: 20%;
      color: #fff;
      background-color: #308ddb;
    }
    span {
      top: 25%;
      left: 4%;
      width: 7%;
      height: 50%;
      background: url("../assets/images/expression.png") no-repeat center / 100% auto;
    }
  }
  /*.chat {*/
    /*position: absolute;*/
    /*right: 0;*/
    /*bottom: 310px;*/
    /*left: 0;*/
    /*overflow: auto;*/
    /*-webkit-overflow-scrolling: touch;*/
    /*.chat-box {*/
      /*.bubblebox {*/
        /*img {*/
          /*width: 100px;*/
          /*height: auto;*/
          /*margin: 0 20px;*/
        /*}*/
        /*p {*/
          /*position: relative;*/
          /*width: 80%;*/
          /*margin: 0 0 60px 10px;*/
        /*}*/
      /*}*/
    /*}*/
  /*}*/
.yo-scroll {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 210px;
  left: 0;
  -webkit-overflow-scrolling: touch;
  background-color: #ddd;
  .inner {
    position: absolute;
    height: 1400px;
    top: 0;
    width: 100%;
    overflow: auto;
    transition-duration: 300ms;
    .bubblebox {
      img {
        width: 100px;
        height: auto;
        margin: 0 20px;
      }
      p {
        position: relative;
        width: 80%;
        margin: 0 0 60px 10px;
        .nickname, .new-time {
          position: absolute;
          top: -50px;
          font-size: 16px;
          color: #969698;
        }
        .new-time-left {
          left: 100px;
        }
        .nickname-right {
          right: 135px;
        }
        .new-time-right {
          right: 0;
        }
        .bkbubble {
          display: inline-block;
          max-width: 90%;
          min-height: 50px;
          padding: 25px 15px;
          font-size: 36px;
          line-height: 50px;
          color: #000004;
          border-radius: 8px;
          background-color: #daebfb;
        }
      }
    }

  }
  .pull-refresh {
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .touch {
    .inner {
      transition-duration: 0ms;
    }
  }
}
.yo-scroll.down .down-tip {
  display: block;
}
.yo-scroll.up .up-tip {
  display: block;
}
.yo-scroll.refresh .refresh-tip {
  display: block;
}
.yo-scroll .down-tip,
.yo-scroll .refresh-tip,
.yo-scroll .up-tip {
  display: none;
}
.yo-scroll .load-more {
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>



